body {
  letter-spacing: .02em;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  font-family: inherit;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  max-width: 100%;
  outline: 1px solid red;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 414px) {
  .container {
    width: 414px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    width: 1024px;
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

:root {
  --logo-theme-dark: #fff;
  --logo-theme-light: #494848;
  --subtitle-color: #b4b4b4;
  --accent-color: #f3c653;
  --primary-text-color: #5f5f5f;
  --secondary-text-color: #7a7a7a;
  --accent-text-color: #c79002;
  --logo-font: "Film P3";
  --main-font: "Montserrat";
  --additional-font: "Times New Roman";
  --background-lighter: #fffdee;
  --background-darker: #f0ecd6;
  --footer-background: #0e0c07;
  --transition-fast: 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-slow: 500ms cubic-bezier(.4, 0, .2, 1);
}

header {
  z-index: 3;
  width: 100%;
  padding-top: 16px;
  position: absolute;
  top: 0;
  right: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  letter-spacing: .3em;
  color: var(--logo-theme-dark);
  font-family: Film P3;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.08333;
  text-decoration: none;
  display: block;
}

.header__nav-list {
  align-items: center;
  gap: 20px;
  display: flex;
}

.header__nav-link {
  letter-spacing: .02em;
  color: var(--accent-color);
  align-self: center;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
}

@media screen and (max-width: 360px) {
  .header__logo {
    font-size: 32px;
  }

  .header__nav-link {
    font-size: 14px;
  }

  .header__nav-list {
    gap: 10px;
  }
}

.header__nav-items {
  gap: 6px;
  display: flex;
}

.header__nav-tel {
  color: var(--logo-theme-dark);
  line-height: 0;
  display: flex;
}

.header__tel-number {
  align-self: center;
  display: none;
}

@media screen and (min-width: 1024px) {
  .header__tel-number {
    display: block;
  }

  .header__nav-tel {
    letter-spacing: .02em;
    border: 1px solid rgba(243, 198, 83, .7);
    border-radius: 2px;
    padding: 8px;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 400;
    line-height: .823529;
  }

  .header__nav-items {
    gap: 32px;
  }

  .header__nav-list {
    gap: 72px;
  }

  .header__nav-link {
    font-size: 20px;
    line-height: 1.2;
  }
}

.header__social-list {
  display: none;
}

@media screen and (min-width: 1280px) {
  .header__social-list {
    gap: 20px;
    display: flex;
  }
}

.swiper {
  height: auto;
  width: 100%;
}

.slider_container {
  padding: 0;
}

.swiper__container {
  position: relative;
}

.swiper-slide {
  width: 100%;
  height: auto;
}

.slider__arrows {
  justify-content: space-between;
  display: flex;
}

.s-button-prev {
  z-index: 2;
  width: 40px;
  height: 60px;
  fill: var(--accent-color);
  background-color: rgba(0, 0, 0, .4);
  border: none;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 1%;
}

.s-button-next {
  z-index: 2;
  width: 40px;
  height: 60px;
  fill: #f3c653;
  background-color: rgba(0, 0, 0, .4);
  border: none;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  right: 1%;
}

.swiper__gradient {
  height: 115px;
  width: 100%;
  z-index: 2;
  background-image: linear-gradient(#000, rgba(0, 0, 0, .55), rgba(0, 0, 0, 0));
  position: absolute;
  top: 0;
}

.room-hero {
